.subscription-card {
  box-sizing: border-box;
  max-width: 360px;
  min-height: 448px;
  background-color: $color-default-white;
  border: solid 7px $color-default-white;

  dd,
  dt {
    font-family: $ff-roboto-condensed;
    text-align: center;
    margin: 0;
    padding: 0;
  }

  dt {
    font-size: 26px;
    line-height: 40px;
    font-weight: 700;
    color: $color-blue-zodiac;
  }

  dd {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: $color-blue-bayoux;
  }

  &__currency {
    display: inline-block;
    vertical-align: baseline;
    width: 100%;
    text-align: center;
  }

  span {
    position: relative;
    font-family: $ff-roboto-condensed;
    font-size: 60px;
    line-height: 90px;
    font-weight: 700;
    color: $subscription-price-color;

    &::after {
      content: attr(data-subscription-price);
      position: absolute;
      font-size: 100px;
      line-height: 150px;
      font-weight: 700;
      color: $subscription-price-shadow-price-color;
      left: 0;
      top: 0;
    }
  }

  .subscription-card__button {
    font-family: $ff-roboto-condensed;
    font-size: 12px;
    line-height: 14px;
    font-weight: 700;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-decoration: none;
    color: $color-blue-zodiac;
    box-shadow: inset 0 0 0 2px $color-blue-zodiac;
  }

  &:hover {
    border-image: $subscription-border-gradient;
    border-image-slice: 1;

    .subscription-card__button {
      box-shadow: none;
      background: $gradient-default;
      color: $color-default-white;
    }
  }
}

.subscription-card__description-list {
  padding: 0;
  margin: 0;
  list-style: none;
}
